import React from "react";
import ModalWrapper from "./ModalWrapper";

const Confirmation = ({ title = "Are you sure?", onConfirm }) => {
  return (
    <ModalWrapper>
      <div className="bg-white p-4 rounded-md">
        <p className="mb-4">{title}</p>
        <div className="flex justify-end">
          <button
            onClick={() => onConfirm(true)}
            className="bg-red-500 text-white py-2 px-4 rounded-md mr-2 transition duration-300 ease-in-out hover:bg-red-600 hover:text-white"
          >
            Yes
          </button>
          <button
            onClick={() => onConfirm(false)}
            className="bg-gray-300 text-gray-800 py-2 px-4 rounded-md transition duration-300 ease-in-out hover:bg-gray-400 hover:text-gray-900"
          >
            No
          </button>
        </div>
      </div>
    </ModalWrapper>
  );
};

export default Confirmation;
