import React from "react";
import { ReactTyped } from "react-typed";
import { useRefState } from "../../Context/RefProvider";

const About = () => {
  const { bookAppointmentRef, scrollToRef } = useRefState();
  return (
    <div
      className="min-h-screen flex flex-col justify-center lg:px-32 px-5 text-black bg-[url('assets/img/home1.png')] bg-no-repeat bg-center bg-cover"
      style={{ opacity: "0.9" }}
    >
      <div className="max-w-[800px] w-full h-screen mx-auto text-center flex flex-col justify-center">
        <h1
          className="md:text-4xl sm:text-3xl text-2xl font-bold md:py-3 text-black"
          style={{ textShadow: "4px 4px 8px rgba(0,0,0,0.5)" }}
        >
          Welcome to Namo Namo Dental Clinic: Your Smile's Best Friend!
        </h1>
        <div className="flex justify-center items-center">
          <p
            className="md:text-3xl sm:text-xl text-lg font-bold py-4 text-black"
            style={{ textShadow: "4px 4px 8px rgba(0,0,0,0.5)" }}
          >
            Comprehensive Dental Services including
          </p>
          <ReactTyped
            className="md:text-3xl sm:text-xl text-lg font-bold md:pl-4 pl-2 text-[#b54b9f]"
            style={{ textShadow: "4px 4px 8px rgba(0,0,0,0.5)" }}
            strings={[
              "General Dentistry",
              "Cosmetic Dentistry",
              "Orthodontics",
              "Pediatric Dentistry",
              "Oral Surgery",
              "Dental Implants",
            ]}
            typeSpeed={120}
            backSpeed={140}
            loop
          />
        </div>
        <div className="max-w-[800px] mt-4 mx-auto text-center">
          <p
            className="md:text-lg text-base font-bold text-black"
            style={{ textShadow: "4px 4px 8px rgba(0,0,0,0.5)" }}
          >
            Namo Namo Dental Clinic is committed to providing top-notch dental
            care services. From routine check-ups to advanced dental treatments,
            we strive to ensure your oral health and well-being. Our experienced
            team of dentists is dedicated to delivering personalized care in a
            comfortable and friendly environment. Let us help you achieve a
            healthy and beautiful smile!
          </p>
        </div>
        <button
          onClick={() => {
            // contactRef.current.scrollIntoView({ behavior: "smooth" });
            scrollToRef(bookAppointmentRef);
          }}
          className="bg-[#70d8bd] hover:bg-[#63c0a3] transition-colors duration-300 w-[200px] rounded-md font-medium my-6 mx-auto py-3 text-black"
        >
          Request Appointment
        </button>
      </div>
    </div>
  );
};

export default About;
