import React, { useState } from "react";
import ModalWrapper from "./ModalWrapper";

const ContactUs = ({ closeForm }) => {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phoneNumber: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };
  return (
    <ModalWrapper>
      <div className="bg-white rounded-xl p-1">
        <form className="w-full md:w-[480px] lg:w-[600px] xl:w-[800px] space-y-5 bg-white p-5 rounded-xl">
          <h1 className="text-3xl font-semibold text-center text-backgroundColor">
            Contact Us
          </h1>
          <div className="flex flex-col">
            <input
              className="py-3 px-2 bg-[#d5f2ec] rounded-lg"
              type="text"
              name="firstName"
              id="userFirstName"
              placeholder="First Name"
              value={formData.firstName}
              onChange={handleChange}
            />
          </div>
          <div className="flex flex-col">
            <input
              className="py-3 px-2 bg-[#d5f2ec] rounded-lg"
              type="text"
              name="lastName"
              id="userLastName"
              placeholder="Last Name"
              value={formData.lastName}
              onChange={handleChange}
            />
          </div>
          <div className="flex flex-col">
            <input
              className="py-3 px-2 bg-[#d5f2ec] rounded-lg"
              type="email"
              name="email"
              id="userEmail"
              placeholder="Your Email"
              value={formData.email}
              onChange={handleChange}
            />
          </div>
          <div className="flex flex-col">
            <input
              className="py-3 px-2 bg-[#d5f2ec] rounded-lg"
              type="number"
              name="phoneNumber"
              id="userNumber"
              placeholder="Phone No."
              value={formData.phoneNumber}
              onChange={handleChange}
            />
          </div>
        </form>
        <div className="flex  gap-5 bg-white px-5 py-2">
          <button
            className="bg-[#4cceac] text-white px-4 py-2 rounded-md hover:bg-hoverColor transition duration-300 ease-in-out"
            onClick={(e) => {
              console.log(formData);
            }}
          >
            Contact Us
          </button>
          <button
            className="bg-[#4cceac] text-white px-4 py-2 rounded-md hover:bg-hoverColor transition duration-300 ease-in-out"
            onClick={closeForm}
          >
            Close
          </button>
        </div>
      </div>
    </ModalWrapper>
  );
};

export default ContactUs;
