import React, { useMemo, useState } from "react";
import AppointmentAnimation from "../../assets/animations/Appointment.json";
import Lottie from "lottie-react";
import { useDispatch, useSelector } from "react-redux";
import { setHasCustomerId } from "../../Slices/authSlice";
import { useNavigate } from "react-router-dom";
import { FaPhoneAlt } from "react-icons/fa";
import mdata from "./MOCK_DATA.json";
import { createColumnHelper } from "@tanstack/react-table";
import MyTable from "../MyTable/MyTable";
const RequestAppointment = () => {
  const data = useMemo(() => mdata, []);
  const columnHelper = createColumnHelper();

  const columns = [
    columnHelper.accessor("id", { header: "ID" }),
    columnHelper.accessor("service_name", { header: "Service Name" }),
    columnHelper.accessor("price", {
      header: "Cost",
      cell: (info) => {
        return <span>{info.row.original.price}{'$'}</span>;
      },
    }),
  ];

  const [formData, setFormData] = useState({
    customerId: "",
    firstName: "",
    lastName: "",
    email: "",
    phoneNumber: "",
    selectedService: "",
    selectedDate: "",
    selectedTime: "",
  });
  const navigate = useNavigate();

  /* Get cusomter Id */
  const customerId = useSelector((state) => state.authState.hasCustomerId);
  const dispatch = useDispatch();
  console.log(customerId);

  const handleCutomerId = () => {
    dispatch(setHasCustomerId(!customerId));
  };

  const services = [
    "Dental Consult",
    "Teeth Cleaning",
    "Teeth Whitening",
    // Add more services as needed
  ];

  // Calculate the end time based on the selected service duration
  const serviceDurations = {
    "Dental Consult": 30, // in minutes
    "Teeth Cleaning": 60, // in minutes
    "Teeth Whitening": 40, // in minutes
    // Add more services and durations as needed
  };

  const fetchAvailableTimes = (date) => {
    const availableTimes = [];
    for (let i = 8; i <= 16; i++) {
      availableTimes.push(`${i}:00`);
    }
    return availableTimes;
  };

  const reverseTimestamp = (timestamp) => {
    const date = new Date(timestamp);

    // Extract the date and time components
    const year = date.getFullYear();
    const month = date.getMonth() + 1; // Months are zero-indexed, so we add 1
    const day = date.getDate();
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const seconds = date.getSeconds();

    // Format the date and time as needed
    const formattedDate = `${year}-${month.toString().padStart(2, "0")}-${day
      .toString()
      .padStart(2, "0")}`;
    const formattedTime = `${hours.toString().padStart(2, "0")}:${minutes
      .toString()
      .padStart(2, "0")}:${seconds.toString().padStart(2, "0")}`;

    console.log("Date:", formattedDate);
    console.log("Time:", formattedTime);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("Form submitted:", formData);

    // Ensure leading zeros for single-digit hours
    const formattedTime = formData.selectedTime.padStart(5, "0"); // Add leading zero if needed

    // Combine date and formatted time strings into a single string
    const dateTimeString = formData.selectedDate + "T" + formattedTime;

    // Create a Date object from the combined date and time string
    const selectedDateTime = new Date(dateTimeString);

    // Get timestamp in milliseconds
    const timestamp = selectedDateTime.getTime();
    const durationInMinutes = serviceDurations[formData.selectedService];

    // Calculate end date and time
    const endDateTime = new Date(
      selectedDateTime.getTime() + durationInMinutes * 60000
    ); // Convert minutes to milliseconds

    // Get end timestamp
    const endTimestamp = endDateTime.getTime();

    console.log("Selected DateTime:", selectedDateTime);
    console.log("End DateTime:", endDateTime);
    console.log("Timestamp for booking:", timestamp);
    console.log("Timestamp for ending:", endTimestamp);

    // Perform further processing if needed
    reverseTimestamp(timestamp);
    reverseTimestamp(endTimestamp);
  };

  return (
    <div className="w-full py-16 text-black px-4 bg-white">
      <div className="max-w-[1240px] gap-10 mx-auto grid lg:grid-cols-3">
        <div className="flex flex-col-reverse lg:col-span-2 text-lg whitespace-normal my-4">
          <p style={{ whiteSpace: "pre-line" }}>
            Taking care of your dental health has never been easier. Request an
            appointment with us today to receive personalized care from our
            experienced team of dentists. From routine checkups to specialized
            treatments, we're here to help you achieve a healthy and beautiful
            smile. Having Difficulty Call Us on <br />
            <br />
          </p>
          <span className="flex item-center text-[#70d8bd]">
            <p href="tel:+977-12345678" className="">
              +977-12345678
            </p>
            <FaPhoneAlt className="inline-block ml-1 mt-1" />
          </span>
          <h1 className="md:text-4xl sm:text-3xl text-2xl font-bold py-2 font-serif">
            Appointment Request
          </h1>
          <div className="pointer-events-none -mb-7 flex justify-center items-center">
            <Lottie animationData={AppointmentAnimation} />
          </div>
        </div>
        <div className="my-4">
          <h2 className="text-2xl ml-3">Our Features</h2>
          <div>
            <MyTable columns={columns} data={data} flg={false}/>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RequestAppointment;
