import React from "react";
import Navbar from "./Navbar";
import { RefProvider, useRefState } from "../../Context/RefProvider";
import About from "./About";
import Services from "./Services";
import Doctors from "./Doctors";
import RequestAppointment from "./RequestAppointment";
import Footer from "./Footer";
import WhatsAppChatButton from "../WhatsApp/WhatsAppChatButton";

const Homepage = () => {
  const { aboutRef, bookAppointmentRef, servicesRef, doctorRef } =
    useRefState();
  return (
    <div>
      <Navbar />
      <main>
        <div ref={aboutRef}>
          <About />
        </div>
        <div ref={servicesRef}>
          <Services/>
        </div>

        <div ref={doctorRef}>
          <Doctors/>
        </div>

        <div ref={bookAppointmentRef}>
          <RequestAppointment/>
        </div>
        <WhatsAppChatButton phoneNumber={'9846897382'}/>
        <Footer/>
      </main>
    </div>
  );
};

export default Homepage;
