import React from "react";
import CustomSlider from "../CustomSlider/CustomSlider";
import ServiceSlide from "../CustomSlider/ServiceSlide";
import BillingAnimation from "../../assets/animations/billing.json";
import DoctorAnimation from "../../assets/animations/doctors.json";
import DoctorConsultAnimation from "../../assets/animations/doctorconsult.json";
import TeethWhitening from "../../assets/animations/teethwhitening.json";


const Services = () => {
  const services = [
    {
      title: "Dental Care",
      description:
        "Our clinic offers comprehensive dental care services, including preventive care, restorative treatments, and cosmetic procedures. We prioritize your oral health and strive to provide personalized care tailored to your needs.",
      src: TeethWhitening,
    },
    {
      title: "Proper Consulting",
      description:
        "We believe in thorough consultations to understand your dental concerns and goals. Our experienced dentists take the time to listen to your needs, explain treatment options, and answer any questions you may have.",
      src: DoctorConsultAnimation,
    },
    {
      title: "Affordable Billing",
      description:
        "We understand the importance of affordability when it comes to dental care. That's why we offer transparent billing practices and work with you to explore payment options that fit your budget.",
      src: BillingAnimation,
    },
    {
      title: "Experienced Doctor Service",
      description:
        "With years of experience and expertise, our team of dentists provides top-notch dental care. From routine checkups to complex procedures, you can trust us to deliver exceptional service with a focus on your comfort and satisfaction.",
      src: DoctorAnimation,
    },
  ];


  return (
    <div className="min-h-screen flex flex-col justify-center lg:px-32 px-5 pt-24 lg:pt-16">
      <div className="flex flex-col items-center lg:flex-row justify-between">
        <div>
          <h1 className="text-4xl font-semibold text-center lg:text-start">
            Dental Services
          </h1>
          <p className="mt-2 text-center lg:text-start">
            At our clinic, we provide a range of dental services to meet your
            needs and ensure your oral health.
          </p>
        </div>
   
      </div>

      <div className="p-10">
        <CustomSlider>
          {services.map((service, index) => (
            <ServiceSlide
              key={index}
              header={service.title}
              description={service.description}
              src={service.src}
            />
          ))}
        </CustomSlider>
        <style>{`
          .slick-prev:before,
          .slick-next:before {
            color: black !important;
          }
        `}</style>
      </div>
    </div>
  );
};

export default Services;
