import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Medicare from "../../../src/assets/medicare_logo.png";
import { AiOutlineClose, AiOutlineMenu } from "react-icons/ai";
import MyStyles from "../CssConfigs/MyStyleConfig";
import ContactUs from "../models/ContactUs";
import Confirmation from "../models/Confirmation";
import { useRefState } from "../../Context/RefProvider";

const Navbar = () => {
  const [menu, setMenu] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const navigate = useNavigate();
  const { aboutRef, bookAppointmentRef, servicesRef, doctorRef, scrollToRef } =
    useRefState();
  function handleClick(link) {
    console.log(link);
  }

  /* Navbar links */
  const links = [
    { to: "about", text: "About Us", ref: aboutRef },
    { to: "services", text: "Services", ref: servicesRef },
    { to: "doctors", text: "Doctors", ref: doctorRef },
    { to: "appointment", text: "Appointment", ref: bookAppointmentRef },
  ];

  const handleChange = () => {
    setMenu(!menu);
  };

  const onConfirm = (confirmation) => {
    if (confirmation === true) console.log("Proceeding..");
    setIsOpen(false);
  };

  const closeMenu = () => {
    if (menu === true) setMenu(!menu);
  };

  const openForm = () => {
    setShowForm(true);
    setMenu(false);
  };

  const closeForm = () => {
    setShowForm(false);
  };

  return (
    <div className="fixed w-full z-10">
      <div className="flex flex-row justify-between p-5 md:px-32 bg-[#000300] text-white">
        <div className="flex flex-row items-center cursor-pointer">
          <img
            /* Put the logo here */
            src={Medicare}
            alt="Logo"
            className="h-6 md:h-8 lg:h-10 xl:h-12 2xl:h-14 w-6 md:w-8 lg:w-10 xl:w-12 2xl:w-14 rounded-full mr-2"
          />
          <Link to="#">
            <h1 className="text-[#70d8bd] md:text-sm lg:text-lg xl:text-xl 2xl:text-2xl font-semibold">
              Namo Namo Clinic
            </h1>
          </Link>
        </div>

        <nav className="hidden lg:flex flex-row items-center text-lg font-medium gap-8">
          <ul className="hidden lg:flex flex-row items-center text-lg font-medium gap-8">
            {links.map((link, index) => (
              <li
                key={index}
                onClick={() => {
                  handleClick(link);
                  scrollToRef(link.ref);
                }}
              >
                <Link className="hover:text-[#b54b9f]">{link.text}</Link>
              </li>
            ))}
          </ul>

          {/* <div>
            <button
              onClick={(e) => openForm()}
              className={MyStyles.button_Deskop}
            >
              Contact Us
            </button>
          </div>  */}

          <div>
            <button
              onClick={(e) => navigate("/auth")}
              className={MyStyles.button_Deskop}
            >
              Login
            </button>
          </div>
        </nav>
        {/* {showForm && <Contact closeForm={closeForm} />} */}
        {showForm && <ContactUs closeForm={closeForm} />}
        {isOpen && <Confirmation onConfirm={onConfirm} />}
        <div className="lg:hidden flex items-center">
          {menu ? (
            <AiOutlineClose
              size={28}
              onClick={handleChange}
              className="text-[#4cceac] text-xl md:text-2xl lg:text-3xl xl:text-4xl 2xl:text-5xl cursor-pointer"
            />
          ) : (
            <AiOutlineMenu
              size={28}
              onClick={handleChange}
              className="text-[#4cceac] text-xl md:text-2xl lg:text-3xl xl:text-4xl 2xl:text-5xl cursor-pointer"
            />
          )}
        </div>

        <div
          /* Logic for smooth navbar */
          className={` ${
            menu ? "translate-x-0" : "-translate-x-full"
          } lg:hidden flex flex-col absolute bg-[#000300] text-white left-0 top-16 font-semibold text-lg md:text-xl lg:text-2xl xl:text-3xl 2xl:text-4xl text-center pt-8 pb-4 gap-5 w-full h-fit transition-transform duration-300`}
        >
          <ul>
            {links.map((link, index) => (
              <li
                key={index}
                onClick={() => {
                  handleClick(link);
                  scrollToRef(link.ref);
                  closeMenu();
                }}
                className="my-2"
              >
                <Link className="hover:text-[#b54b9f]">{link.text}</Link>
              </li>
            ))}
            {/* <div className="lg:hidden">
              <button
                onClick={(e) => openForm()}
                className={MyStyles.button_Mobile}
              >
                Contact Us
              </button>
            </div> */}
            <div>
              <button
                onClick={(e) => navigate("/auth")}
                className={MyStyles.button_Deskop}
              >
                Login
              </button>
            </div>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
